.item {
    font-family: "Trebuchet MS", "Lucida Sans";
    padding: 7px 20px;
    margin-bottom: 10px;
    border-radius: 5px;
    border-left: 10px solid #f05d22;
    box-shadow: 2px -2px 5px 0 rgba(0, 0, 0, 0.1),
        -2px -2px 5px 0 rgba(0, 0, 0, 0.1), 2px 2px 5px 0 rgba(0, 0, 0, 0.1),
        -2px 2px 5px 0 rgba(0, 0, 0, 0.1);
    font-size: 20px;
    letter-spacing: 2px;
    transition: 0.3s all linear;
}
.item:nth-child(2) {
    border-color: #8bc63e;
}
.item:nth-child(3) {
    border-color: #fcba30;
}
.item:nth-child(4) {
    border-color: #1ccfc9;
}
.item:nth-child(5) {
    border-color: #493224;
}
.item:hover {
    border-left: 10px solid transparent;
}
.item:nth-child(1):hover {
    border-right: 10px solid #f05d22;
}
.item:nth-child(2):hover {
    border-right: 10px solid #8bc63e;
}
.item:nth-child(3):hover {
    border-right: 10px solid #fcba30;
}
.item:nth-child(4):hover {
    border-right: 10px solid #1ccfc9;
}
.item:nth-child(5):hover {
    border-right: 10px solid #493224;
}
